import { changesInputs } from "./data.js";

const debugInput = (input) => {
  return changesInputs[input] || input;

  /*  Utilizamos el operador l√≥gico OR ( ||) para obtener changesInputs[input]. 
      Si existe, se devuelve; de lo contrario, se devuelve input.  
  */

  /* Forma Anterior 
    let newData = "";
  input in changesInputs ? (newData = changesInputs[input]) : (newData = input);

  return newData;
  */
};

export default debugInput;
